.content-center {
  display: flex;
  align-items: center;
  justify-content: center;

  margin: 0;
}

.languages-section {
  width: 70%;
}

.title-text {
  width: 100%;
}

.card-spacer {
  margin: 2rem 0 1rem;
}

.column-layer {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
}

.column-element {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
}

.contact-icon {
  height: 15vh;
  /* width: 30%; */
  width: 50%;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 780px) {
  .bullet-text {
    font-size: 1.5rem;
  }
}